<template>
  <div id="navbar" class="d-flex">
    <v-nav image="images/ic-system.png" lable="System" path="/system" :class="navitem_class[0]"/>
    <v-nav image="images/ic-download.png" lable="Download" v-if="isAdmin" path="/download" :class="navitem_class[1]"/>
    <v-nav image="images/ic-log.png" lable="Log" path="/log" :class="navitem_class[2]"/>
    <v-nav image="images/ic-user.png" lable="User" path="/user" v-if="isAdmin" :class="navitem_class[3]"/>

    <a class="my-auto ms-auto me-0 navritem myinfo" v-b-modal.myinfo-admin-modal v-if="$store.getters.isAdmin"><span>My Info</span></a>
    <a class="my-auto ms-auto me-0 navritem myinfo" v-b-modal.myinfo-modal v-if="!$store.getters.isAdmin"><span>My Info</span></a>
    <a class="my-auto ms-0 me-0 navritem logout" v-on:click="logout"><span>Logout</span></a>

<!-- My Info popup for admin -->
    <b-modal id="myinfo-admin-modal" centered header-class="p-0 custom-modal-header" footer-class="p-0 custom-modal-footer" dialog-class="custom-modal-dialog2" content-class="custom-modal-content" v-if="$store.getters.isAdmin">
      <template #modal-header>
        <div class="modal-header">My information</div>
      </template>
      
      <div class="d-block mt-16 mb-16">
        <div class="d-flex">
          <div class="label subtitle1 mr-16">ID</div>
          <div class="content">{{$store.getters.userid}}</div>
        </div>
        <div class="d-flex mt-24">
          <div class="label subtitle1 mr-16">Type</div>
          <div class="content">administrator</div>
        </div>
      </div>

      <template #modal-footer>
        <div class="d-flex align-items-stretch modal-footer">
          <button @click="hidePopup()" class="flex-fill modal-btn cancel-admin">
            Close
          </button>
        </div>
      </template>
    </b-modal>

<!-- My Info popup -->
    <b-modal id="myinfo-modal" centered header-class="p-0 custom-modal-header" footer-class="p-0 custom-modal-footer" dialog-class="custom-modal-dialog" content-class="custom-modal-content" v-if="!$store.getters.isAdmin" v-on:hidden="hidePopup">
      <template #modal-header>
        <div class="modal-header">My information</div>
      </template>
      <form autocomplete="off">
      <div class="d-block">

        <div class="d-flex modal-body-row mt-8">
          <div class="modal-body-label">ID</div>
          <div class="modal-body-content">{{$store.getters.userid}}</div>
        </div>

        <div class="d-flex modal-body-row mt-16">
          <div class="modal-body-label">Type</div>
          <div class="modal-body-content">{{$store.getters.userType}}</div>
        </div>

        <div class="d-flex modal-body-row mt-16">
          <div class="modal-body-label">Current password</div>
          <div class="modal-body-content d-flex">
            <input autocomplete="new-password" :type="cpwd" v-model="currentPassword" class="flex-fill input-modal2" />
          </div>
        </div>

        <div class="d-flex modal-body-row mt-16">
          <div class="modal-body-label">New password</div>
          <div class="modal-body-content d-flex">
            <input autocomplete="new-password" v-model="newPassword1" :type="npwd" class="flex-fill input-modal" />
            <button class="flex-fill bnt-pwd-modal" v-on:click="npwd = (npwd === 'password') ? 'text' : 'password'">
              <img class="btn-ic" src="../assets/images/ic-show-pwd.png" v-if="npwd === 'password'"/>
              <img class="btn-ic" src="../assets/images/ic-hide-pwd.png" v-if="npwd !== 'password'" />
            </button>
          </div>
        </div>

        <div class="d-flex modal-body-row mt-16 mb-16">
          <div class="modal-body-label">Confirm new password</div>
          <div class="modal-body-content d-flex">
            <input autocomplete="new-password" v-model="newPassword2" :type="cnpwd" class="flex-fill input-modal" />
            <button class="flex-fill bnt-pwd-modal" v-on:click="cnpwd = (cnpwd === 'password') ? 'text' : 'password'">
              <img class="btn-ic" src="../assets/images/ic-show-pwd.png" v-if="cnpwd === 'password'"/>
              <img class="btn-ic" src="../assets/images/ic-hide-pwd.png" v-if="cnpwd !== 'password'" />
            </button>
          </div>
        </div>
      </div>
      </form>
      <template #modal-footer>
        <div class="d-flex align-items-stretch modal-footer">
          <button @click="apply()" class="flex-fill modal-btn reset">
            Edit
          </button>
          <button @click="hidePopup()" class="flex-fill modal-btn cancel">
            Cancel
          </button>
        </div>
      </template>
    </b-modal>

<!-- Error popup -->
    <b-modal id="nav-err-modal" centered header-class="p-0 custom-modal-header" footer-class="p-0 custom-modal-footer" dialog-class="custom-modal-dialog2" content-class="custom-modal-content" >
      <template #modal-header>
        <div class="modal-header">Alert</div>
      </template>
      
      <div class="d-block">
        <div class="d-flex justify-content-center">
          <img src="../assets/images/img-alert.png" class="mx-auto"/>
        </div>
        <div class="modal-desc">
          {{errorMessage}}
        </div>
      </div>

      <template #modal-footer>
        <div class="d-flex align-items-stretch modal-footer">
          <button @click="closeError()" class="flex-fill modal-btn cancel">
            Close
          </button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import Nav from './Nav.vue'
import { mapGetters } from 'vuex'
import axios from 'axios'

export default {
  name: 'Navbar',
  data() {
    return {
      errorMessage: 'ERR',
      navitem_class: ['navitem-selected', 'navitem', 'navitem', 'navitem'],
      cpwd: 'password',
      npwd: 'password',
      cnpwd: 'password',
      currentPassword: '',
      newPassword1: '',
      newPassword2: ''
    }
  },
  mounted() {
  },
  created() {
    this.navitem_class[0] = (this.$route.path == '/system') ? 'navitem-selected' : 'navitem'
    this.navitem_class[1] = (this.$route.path == '/download') ? 'navitem-selected' : 'navitem'
    this.navitem_class[2] = (this.$route.path == '/log') ? 'navitem-selected' : 'navitem'
    this.navitem_class[3] = (this.$route.path == '/user') ? 'navitem-selected' : 'navitem'
  },
  props: {
    current: String
  },
  computed: {
    ...mapGetters([
      'isAdmin'
    ])
  },
  methods: {
    logout() {
      axios({ // 세션 삭제
      method: 'delete',
      url: this.$store.getters.apiServer + '/php/sessions.php',
      params: {
        key: this.$store.getters.sessionKey
      },
      }).then (() => {
        this.$store.commit('logout')
        this.$router.push('Login')
      }).catch(err => {
        console.log(err);
      })
    },
    hidePopup() {
      this.newPassword1 = ''
      this.newPassword2 = ''
      this.currentPassword = ''
      this.$bvModal.hide('myinfo-admin-modal')
      this.$bvModal.hide('myinfo-modal')
    },
    apply() {
      if (!this.currentPassword || this.currentPassword === '') {
        this.errorMessage = 'Check the current password!'        
        this.$bvModal.show('nav-err-modal')
      } else if (!this.newPassword1 || this.newPassword1.length === '' || this.newPassword1.length > 8 || this.newPassword1 !== this.newPassword2) {
        this.errorMessage = 'Check the new password!'        
        this.$bvModal.show('nav-err-modal')
      } else {
        axios({
          method: 'put',
          url: this.$store.getters.apiServer + '/php/password.php',
          params: {
            key: this.$store.getters.sessionKey,
            id: this.$store.getters.userid
          },
          data: {
            currentPassword: this.currentPassword,
            newPassword: this.newPassword1
          }
        }).then(resp => {
          if (!(resp.status == 200 && resp.data.state == 'Success')) {
            this.errorMessage = 'Failed to change password.'        
            this.$bvModal.show('nav-err-modal')
          }
          this.newPassword1 = ''
          this.newPassword2 = ''
          this.currentPassword = ''
          this.$bvModal.hide('myinfo-modal')
        }).catch(err => {
          this.newPassword1 = ''
          this.newPassword2 = ''
          this.currentPassword = ''
          this.$bvModal.hide('myinfo-modal')
          this.errorMessage = err.message
          this.$bvModal.show('nav-err-modal')
        })
      }
    },
    closeError() {
      this.$bvModal.hide('nav-err-modal')
    }
  },
  components: {
    'v-nav': Nav
  }
}
</script>

